.Movie-Container {
  background-color: black;
  display: flex;
  flex-wrap: wrap;
  height: 100%;
  justify-content: space-evenly;
  margin: 0 auto;
  overflow: scroll;
  padding: 50px 0;
  position: absolute;
  top: 176px;
  width: 100%;
}

.no-favs {
  color: white;
  flex-direction: column;
  justify-content: start;
  text-align: center;
}

.spinner {
  height: 200px;
  margin-top: 25vh;
}

.walter {
  margin: 16vh auto 20px auto;
  width: 25%;
}

@media screen and (max-width: 800px) {
  .Movie-Container {
    top: 280px;
  }
}
