.navBar {
  background-color: #e8e8e8;
  display: flex;
  height: 56px;
  justify-content: space-evenly;
}

.buttons {
  background: lightgrey;
  border: 4px solid black;
  border-radius: 30px;
  bottom: 10px;
  color: black;
  cursor: pointer;
  font-size: 18px;
  font-weight: bolder;
  margin: 10px 0;
  padding: 4px;
  position: relative;
  text-align: center;
  text-decoration: none;
  width: 22%;
}

.buttons:hover {
  background: grey;
  color: lightgrey;
  transition: all 0.4s ease-in-out;
}

.error-msg {
  bottom: 20px;
  color: red;
  position: relative;
  text-align: center;
}

@media screen and (max-width: 800px) {
  .navBar {
    align-items: center;
    flex-direction: column;
    height: 160px;
  }

  .buttons {
    width: 40%;
  }

  .user-name {
    bottom: 10px;
    position: relative;
  }
  .error-msg {
    bottom: 20px;
    color: red;
    position: relative;
    text-align: center;
  }
}
